import React, { useEffect, useState } from "react";
import "./aptitudes.scss";
import LoadPanel from "devextreme-react/load-panel";
import DataGrid from "devextreme-react/data-grid";
import { disenio } from "../../data/disenio.js";

// eslint-disable-next-line
function Disenio() {
  let [loading, setLoading] = useState(true);
  let refAccordion = React.useRef();
  refAccordion.current?.instance.expandItem(0);
  refAccordion.current?.instance.expandItem(1);
  refAccordion.current?.instance.expandItem(2);
  useEffect(() => {
    setTimeout(() => {
      refAccordion.current?.instance.collapseItem(0);
      refAccordion.current?.instance.collapseItem(1);
      refAccordion.current?.instance.collapseItem(2);
      setLoading(false);
    }, 750);
  });
  return (
    <React.Fragment>
      <LoadPanel visible={loading} />
      <h2 className={"content-block"}>Dise√±o</h2>
      <div className={"content-block"}>
        <div className={"dx-card responsive-paddings"}>
          <DataGrid
            className={"dx-card wide-card"}
            dataSource={disenio}
            showBorders={true}
            columnAutoWidth={true}
            columnHidingEnabled={true}
            wordWrapEnabled={true}
            // style={{ whiteSpace: "nowrap" }}
          ></DataGrid>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Disenio;
