import React, { useRef, useEffect } from "react";
import Form, {
  SimpleItem,
  ButtonItem,
  ButtonOptions,
  RequiredRule,
} from "devextreme-react/form";
import {
  loadCaptchaEnginge,
  LoadCanvasTemplate,
  // LoadCanvasTemplateNoReload,
  validateCaptcha,
} from "react-simple-captcha";

function Contacto() {
  let formRef = useRef();

  useEffect(() => {
    setTimeout(() => {
      loadCaptchaEnginge(6);
    }, 500);
  }, []);

  let handleSub = () => {
    var sendmail = require("sendmail")({
      smtpHost: "smtp.gmail.com",
      smtpPort: 25,
    });

    let form = formRef.current?.instance;
    const { Nombre, Empresa, Email, Mensaje } = form.option("formData");
    form.validate();
    console.log(Nombre, Empresa, Email, Mensaje, sendmail);
    if (!form.validate().isValid) return;

    let user_captcha_value = document
      .getElementById("user_captcha_input")
      .value.trim();

    if (validateCaptcha(user_captcha_value, false) === true) {
      console.log("Captcha Matched::::");
      sendmail(
        {
          from: "smtp.gmail.com",
          to: "tavogandini@gmail.com",
          subject: "Test sendmail",
          html:
            "Mail of test sendmail " +
            Nombre +
            " / " +
            Empresa +
            " / " +
            Email +
            " / " +
            Mensaje,
        },
        function (err, reply) {
          console.log("ERROR MAIL::", err && err.stack);
          console.dir(reply);
        }
      );
      form.resetValues();
    } else {
      console.log("Captcha Does Not Match-----");
    }

    document.getElementById("user_captcha_input").value = "";
  };
  return (
    <React.Fragment>
      <h2 className={"content-block"}>Contacto</h2>
      <div className={"content-block"}>
        <div className={"dx-card responsive-paddings"}>
          <Form id={"form"} labelLocation={"top"} ref={formRef}>
            <SimpleItem dataField="Nombre">
              <RequiredRule message="" />
            </SimpleItem>
            <SimpleItem dataField="Empresa" />
            <SimpleItem dataField="Email">
              <RequiredRule message="" />
            </SimpleItem>
            <SimpleItem dataField="Mensaje">
              <RequiredRule message="" />
            </SimpleItem>
            <SimpleItem>
              <div style={{ display: "flex", float: "right" }}>
                <LoadCanvasTemplate /> -
                <input
                  type="text"
                  id="user_captcha_input"
                  style={{ height: "32px" }}
                />
              </div>
            </SimpleItem>

            <ButtonItem alignment="left">
              <ButtonOptions text="Enviar" onClick={() => handleSub()} />
            </ButtonItem>
          </Form>

          <br />
        </div>
      </div>
    </React.Fragment>
  );
}

export default Contacto;
