import React, { useEffect, useState } from "react";
import "./aptitudes.scss";
import LoadPanel from "devextreme-react/load-panel";
import DataGrid from "devextreme-react/data-grid";
import { Accordion, Item } from "devextreme-react/accordion";
import { frontend, backend, bbdd } from "../../data/software.js";

// eslint-disable-next-line
function Software() {
  let [loading, setLoading] = useState(true);
  let refAccordion = React.useRef();
  refAccordion.current?.instance.expandItem(0);
  refAccordion.current?.instance.expandItem(1);
  refAccordion.current?.instance.expandItem(2);
  useEffect(() => {
    setTimeout(() => {
      refAccordion.current?.instance.collapseItem(0);
      refAccordion.current?.instance.collapseItem(1);
      refAccordion.current?.instance.collapseItem(2);
      setLoading(false);
    }, 750);
  });
  return (
    <React.Fragment>
      <LoadPanel visible={loading} />
      <h2 className={"content-block"}>Software</h2>
      <div className={"content-block"}>
        <div className={"dx-card responsive-paddings"}>
          <Accordion
            className={"accordion"}
            ref={refAccordion}
            collapsible={true}
            multiple={true}
            animationDuration={300}
          >
            <Item title="Front-End">
              <DataGrid
                className={"dx-card wide-card"}
                dataSource={frontend}
                showBorders={true}
                columnAutoWidth={true}
                columnHidingEnabled={true}
                wordWrapEnabled={true}
                // style={{ whiteSpace: "nowrap" }}
              ></DataGrid>
            </Item>
            <Item title="Back-End">
              <DataGrid
                className={"dx-card wide-card"}
                dataSource={backend}
                showBorders={false}
                columnAutoWidth={true}
                columnHidingEnabled={true}
                wordWrapEnabled={true}
                // style={{ whiteSpace: "nowrap" }}
              ></DataGrid>
            </Item>
            <Item title="Base de datos">
              <DataGrid
                className={"dx-card wide-card"}
                dataSource={bbdd}
                showBorders={false}
                columnAutoWidth={true}
                columnHidingEnabled={true}
                wordWrapEnabled={true}
                // style={{ whiteSpace: "nowrap" }}
              ></DataGrid>
            </Item>
          </Accordion>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Software;
