import { withNavigationWatcher } from "./contexts/navigation";
import {
  HomePage,
  Aptitudes,
  Studies,
  Software,
  Disenio,
  Trayectoria,
  Contact,
} from "./pages";

const routes = [
  {
    path: "/home",
    component: HomePage,
  },
  {
    path: "/studies",
    component: Studies,
  },
  {
    path: "/trayectoria",
    component: Trayectoria,
  },
  {
    path: "/aptitudes/software",
    component: Software,
  },
  {
    path: "/aptitudes/disenio",
    component: Disenio,
  },
  {
    path: "/admin/aptitudes",
    component: Aptitudes,
    api: true,
  },
  {
    path: "/contact",
    component: Contact,
    api: true,
  },
];

export default routes.map((route) => {
  return {
    ...route,
    component: withNavigationWatcher(route.component),
  };
});
